<template>
  <div id="home" class="pa-4 black">
     <adh :posters="posters" />
    <v-container fluid>
      <v-alert prominent type="error" v-if="errored">
        <v-row align="center">
          <v-col class="grow">
            <div class="title">Error!</div>
            <div>
              오류가 났습니다. 다시 시도하시기를 클릭하십시오.
            </div>
          </v-col>
          <v-col class="shrink">
            <v-btn @click="getVideos">새로 고치다</v-btn>
          </v-col>
        </v-row>
      </v-alert>

      <main v-else>
        <h3 class="headline font-weight-medium">모든 영상</h3>
        <v-row>
          <v-col
            cols="12"
            sm="6"
            md="4"
            lg="3"
            v-for="(video, i) in videos"
           
            :key="i"
            class="mx-xs-auto"
          >
          <!--  v-for="(video, i) in loading ? 12 : videos" -->
            <v-skeleton-loader type="card-avatar" :loading="loading">
              <video-card
                :card="{ maxWidth: 350 }"
                :video="video"
                :channel="video.userId"
              ></video-card>
            </v-skeleton-loader>
          </v-col>
          <v-col class="text-center" v-if="videos.length === 0 && !loading">
            <p>No videos yet</p>
          </v-col>

          <!-- <v-col cols="12" sm="12" md="12" lg="12">
            <infinite-loading @infinite="getVideos">
              <div slot="spinner">
                <v-progress-circular
                  indeterminate
                  color="red"
                ></v-progress-circular>
              </div>
              <div slot="no-results"></div>
              <span slot="no-more"></span>
              <div slot="error" slot-scope="{ trigger }">
                <v-alert prominent type="error">
                  <v-row align="center">
                    <v-col class="grow">
                      <div class="title">Error!</div>
                      <div>
                        Something went wrong, but don’t fret — let’s give it
                        another shot.
                      </div>
                    </v-col>
                    <v-col class="shrink">
                      <v-btn @click="trigger">Take action</v-btn>
                    </v-col>
                  </v-row>
                </v-alert>
              </div>
            </infinite-loading>
          </v-col> -->
        </v-row>
      </main>
    </v-container>
    <div class="text-center">
      <v-pagination
        v-model="page"
        :length="totalPage"
        :total-visible="7"
        circle
        @input="pageChange"
      ></v-pagination>
    </div>
  </div>
</template>

<script>
// import InfiniteLoading from 'vue-infinite-loading'
import moment from 'moment'
import Adh from '@/components/Adh'
import VideoCard from '@/components/VideoCard'
import VideoService from '@/services/VideoService'

export default {
  name: 'V1',
  data: () => ({
    loading: true,
    loaded: false,
    errored: false,
    videos: [],
    totalPage:1,
    page: 1,
    posters:[],
    typee:1
  }),
  methods: {
    pageChange(newPage){
      this.videos.length=0;
      this.page=newPage
    },
    async getVideos($state) {
      if (!this.loaded) {
        this.loading = true
      }
      const videos = await VideoService.getAll('public', { page: this.page *20,classify:10})
        .catch((err) => {
          console.log(err)
          this.errored = true
        })
        .finally(() => {
          this.loading = false
        })

      if (typeof videos === 'undefined') return

      if (videos.data.data.length) {
        // this.page += 1
        
        this.totalPage=parseInt(videos.data.totalPage/20)
        

        this.videos.push(...videos.data.data)
        // $state.loaded()
        // this.loaded = true
        console.log(this.$state)
      } else {
        $state.complete()
      }
    },
    async getAds(){
      const pos = await VideoService.getPosters({ typee:this.typee})
      console.log(pos)
      pos.data.data.forEach(element => {
        this.posters.push(element)
      });
    },
    dateFormatter(date) {
      return moment(date).fromNow()
    }
  },
  mounted(){
    this.getVideos(this.$state);
    this.getAds()
  },
  components: {
    VideoCard,
    Adh 
  },
  watch:{
    page:{
      handler(val){
        if(val){
          this.getVideos(this.$state)
        }
      }
    }
  }
}
</script>

<style lang="scss">
.card {
  background: #f9f9f9 !important;
}
</style>
